import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { Button, Modal, Table } from 'antd'
import PropTypes from 'prop-types'

import { nodeSelector } from '../../../selectors/nodeList'

import { COLUMNS } from './constants'

const PriceNodeList = ({ isVisible, modalToggleVisible, setPriceNodeList, priceNodeVal }) => {
  const [localPriceNodeVal, setLocalPriceNodeVal] = useState(priceNodeVal)
  const priceNodeData = useSelector(nodeSelector)

  const onSelectChange = (key, row) => {
    setLocalPriceNodeVal(row[0].Id)
  }
  const rowSelection = {
    onChange: onSelectChange,
    type: 'radio',
    selectedRowKeys: [priceNodeVal],
  }
  const sendKey = () => {
    setPriceNodeList(localPriceNodeVal)
    modalToggleVisible()
  }

  return (
    <>
      <Modal title="Price Node List " visible={isVisible} onCancel={modalToggleVisible} footer={null} width={1000}>
        <Table
          rowSelection={rowSelection}
          style={{ width: '90em' }}
          columns={COLUMNS}
          dataSource={priceNodeData}
          scroll={{ x: 1500, y: 'overflow' }}
          pagination
        />
        <Button type="primary" role="button" style={{ marginRight: '2px' }} onClick={sendKey}>
          OK
        </Button>

        <Button role="button" onClick={modalToggleVisible}>
          Cancel
        </Button>
      </Modal>
    </>
  )
}

export default PriceNodeList
PriceNodeList.propTypes = {
  setPriceNodeList: PropTypes.func,
  modalToggleVisible: PropTypes.func,
  isVisible: PropTypes.bool,
  priceNodeVal: PropTypes.number,
}
PriceNodeList.defaultProps = {
  priceNodeData: {},
}
