import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Modal, notification } from 'antd'
import PropTypes from 'prop-types'
import { FormBuilder } from 'rc-form-pure'
import { unwrapResult } from '@reduxjs/toolkit'

import { saveGtpAction } from '../../store/slices/gtpSlice'
import { Button } from '../ui/Button'
import { formatKeys } from '../../helpers/forms'
import { FIELDS as VOLTAGE_FIELDS } from '../../constants/voltage'
import PriceNodeList from '../../pages/Calculator/PriceNodeList/PriceNodeList'
import { gtpSelector } from '../../selectors/gtp'

import { CalibrationSettings } from './controls/CalibrationSettings'
import { Indicators } from './controls/Indicators'
import { Name } from './controls/Name'
import { OremSettings } from './controls/OremSettings'
import { RreSettings } from './controls/RreSettings'
import { VoltageLevels } from './controls/VoltageLevels'
import styles from './AddObject.module.less'

export const AddObject = ({ isVisible, onModalClose, isNew }) => {
  const dispatch = useDispatch()
  const currentObject = useSelector(gtpSelector)
  const [isModalVisible, setIsModalVisible] = useState()
  const [priceNodeVal, setPriceNodeVal] = useState()

  useEffect(() => {
    setPriceNodeVal(!isNew ? (currentObject.ProfileList?.length ? currentObject.ProfileList[0].PriceNodeId : '') : '')
  }, [setPriceNodeVal, currentObject.ProfileList, isNew])

  const priceNodeListTableToggle = () => {
    setIsModalVisible(!isModalVisible)
  }
  const setPriceNodeList = key => {
    setPriceNodeVal(key)
  }

  const handleCancel = () => {
    onModalClose()
  }
  const openNotification = message => {
    notification.open({
      message,
    })
  }
  const handleSubmit = (values, errors) => {
    if (errors) {
      return
    }

    if (!isNew) {
      dispatch(saveGtpAction({ ...currentObject, ...values, PriceNodeId: priceNodeVal }))
        .then(unwrapResult)
        .then(() => {
          openNotification('Object updated successfuly')
          onModalClose()
        })
        .catch(() => {
          openNotification('Error: Please try again ')
        })
    } else {
      const formattedFormKeys = formatKeys(values)
      const valuesToSubmit = {}

      valuesToSubmit.Name = formattedFormKeys.Name
      valuesToSubmit.SupplierId = formattedFormKeys.SupplierId
      valuesToSubmit.PowerCategory = formattedFormKeys.PowerCategory
      valuesToSubmit.PriceCategory = formattedFormKeys.PriceCategory
      const profile = []
      const calibration = {}
      calibration.PriceNodeId = parseInt(priceNodeVal)
      profile.push(calibration)
      valuesToSubmit.ProfileList = profile

      valuesToSubmit[VOLTAGE_FIELDS.transferPriceTable.fieldKey] = {}
      Object.entries(formattedFormKeys[VOLTAGE_FIELDS.transferPriceTable.fieldKey]).forEach(([_, value]) => {
        valuesToSubmit[VOLTAGE_FIELDS.transferPriceTable.fieldKey][value.voltage] = value.tariff
      })

      dispatch(saveGtpAction(valuesToSubmit))
        .then(unwrapResult)
        .then(() => {
          openNotification('Object created successfuly')
          onModalClose()
        })
        .catch(() => {
          openNotification('Error. Please try again')
        })
    }
  }

  return (
    <Modal
      title={!isNew ? `Edit Object ${currentObject.Name}` : 'Add New Object'}
      visible={isVisible}
      onCancel={handleCancel}
      footer={null}
      width={500}
    >
      {isVisible && (
        <FormBuilder withForm onSubmit={handleSubmit}>
          {!isNew ? (
            <>
              <Name name={currentObject.Name} />
              <RreSettings data={currentObject} />
              <VoltageLevels transferPriceTable={currentObject.TransferPriceTable} />
              <OremSettings data={currentObject} />
              <CalibrationSettings data={currentObject?.Preferences?.CalibrationPreference} />
              <Indicators />
            </>
          ) : (
            <>
              <Name />
              <RreSettings />
              <VoltageLevels />

              <OremSettings />
              <CalibrationSettings />
              <Indicators />
            </>
          )}
          <div className={styles.nodebutton}>
            <Button type="primary" onClick={priceNodeListTableToggle}>
              Price Node List
            </Button>
            <span className={styles.priceNodeVal}>{priceNodeVal}</span>
            <br />
          </div>
          <div>
            <Button block type="primary" htmlType="submit" size="large">
              Сохранить
            </Button>
          </div>

          <PriceNodeList
            priceNodeVal={priceNodeVal}
            setPriceNodeList={setPriceNodeList}
            isVisible={isModalVisible}
            modalToggleVisible={priceNodeListTableToggle}
          />
        </FormBuilder>
      )}
    </Modal>
  )
}

AddObject.propTypes = {
  onModalClose: PropTypes.func.isRequired,
  isVisible: PropTypes.bool,
  isNew: PropTypes.bool,
}
