import { createAsyncThunk, createSlice } from '@reduxjs/toolkit'

import { getNode } from '../../../api/nodeListTable'
export const priceNodeListSlice = createSlice({
  name: 'priceNodeListSlice',
  initialState: {
    tableData: [],
  },
  reducers: {
    tableDataReceived: (table, { payload }) => {
      table.tableData = payload.newData
    },
  },
})

export const getTableData = createAsyncThunk('GET_PRICE_NODE_LIST', async (params, thunkApi) => {
  const response = await getNode(params)
  const { data } = response
  const tableRows = data.PriceNodeList
  const newData = tableRows.map(eachRow => {
    eachRow.key = eachRow.Id
    return eachRow
  })

  thunkApi.dispatch(priceNodeListSlice.actions.tableDataReceived({ newData }))
})
