import React, { useCallback, useEffect, useState } from 'react'
import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'
import { format } from 'date-fns'
import { useSelector } from 'react-redux'
import { Space } from 'antd'

import { RangePicker } from '../../../../components/ui/RangePicker/RangePicker'
import { gtpSelector } from '../../../../selectors/gtp'
import { DATE_API_FORMAT } from '../../../../constants/dates'
import { getChart } from '../../../../api/chart'

import styles from './styles.module.less'

const options = {
  chart: {
    backgroundColor: '#E4E4E4',
    spacing: 30,
    style: {
      fontFamily: 'inherit',
    },
    zoomType: 'xy',
  },
  pane: {
    background: [
      {
        backgroundColor: 'green',
        borderColor: '#cccccc',
        borderWidth: 1,
      },
    ],
  },
  title: {
    text: '',
  },
  series: [],
  xAxis: {
    visible: false,
  },
  yAxis: {
    title: {
      text: 'МВт*ч',
      rotation: 0,
      align: 'high',
      margin: 0,
      offset: 0,
      y: -36,
      x: -5,
      style: {
        fontSize: '10px',
        color: '#3C4652',
        fontWeight: '500',
      },
    },
    labels: {
      style: {
        fontSize: '10px',
        color: '#3C4652',
        fontWeight: '500',
      },
    },
    gridLineColor: '#AAAAAA',
    tickInterval: 40,
  },
  legend: {
    enabled: false,
  },
  credits: {
    enabled: false,
  },
}
export const Chart = () => {
  const selectedGtp = useSelector(gtpSelector)
  const [localOptions, setLocalOptions] = useState(options)

  const generateNextOptions = useCallback(async params => {
    const response = await getChart(params)
    const { data } = response

    setLocalOptions({
      ...options,
      series: data?.TimeSeriesRows?.map(item => {
        const seriesData = []
        Object.values(item.History).forEach(value => {
          seriesData.push(...value.Values)
        })

        return {
          data: seriesData,
          name: item.Name,
          color: item.Color,
        }
      }),
    })
  }, [])

  const handleChange = useCallback(
    values => {
      let startDate = null
      let endDate = null

      if (values) {
        startDate = format(values[0], DATE_API_FORMAT)
        endDate = format(values[1], DATE_API_FORMAT)
      }

      generateNextOptions({ id: selectedGtp.Id, beginPeriod: startDate, endPeriod: endDate })
    },
    [generateNextOptions, selectedGtp.Id]
  )

  useEffect(() => {
    if (selectedGtp.Id) {
      generateNextOptions({ id: selectedGtp.Id })
    }
  }, [generateNextOptions, selectedGtp.Id])

  return (
    <div className={styles.container}>
      <div className={styles.alignEnd}>
        <Space direction="vertical" size={12}>
          <RangePicker onChange={handleChange} />
        </Space>
      </div>

      <HighchartsReact highcharts={Highcharts} options={localOptions} />
    </div>
  )
}
