import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'

import { gtpSelector } from '../../../selectors/gtp'
import { ReactComponent as EditIcon } from '../../../components/ui/icons/edit.svg'
import { AddObject } from '../../../components/AddObject/AddObject'
import { getGtpAction } from '../../../store/slices/gtpSlice'

import { CalibrationSettings } from './controls/CalibrationSettings'
import { Indicators } from './controls/Indicators'
import { Name } from './controls/Name'
import { OremSettings } from './controls/OremSettings'
import { RreSettings } from './controls/RreSettings'
import { VoltageLevels } from './controls/VoltageLevels'
import { Container } from './controls/Container'
import styles from './SideBar.module.less'

export const SideBar = () => {
  const dispatch = useDispatch()
  const selectedGtp = useSelector(gtpSelector)

  useEffect(() => {
    dispatch(getGtpAction({ id: selectedGtp.Id }))
  }, [dispatch, selectedGtp.Id])
  const [isModalVisible, setIsModalVisible] = useState(false)

  const editObject = () => {
    if (Object.keys(selectedGtp).length) {
      setIsModalVisible(true)
    }
  }

  return (
    <div className={styles.container}>
      <Container>
        <button size="large" type="primary" className={styles.title} role="button" onClick={editObject}>
          Edit <EditIcon className={styles.titleIcon} />
        </button>
      </Container>
      <Name name={selectedGtp.Name} />
      <RreSettings data={selectedGtp} />
      <VoltageLevels transferPriceTable={selectedGtp.TransferPriceTable} />
      <OremSettings data={selectedGtp} />
      <CalibrationSettings data={selectedGtp?.Preferences?.CalibrationPreference} />
      <Indicators />
      <Container>
        Price Node Id: {selectedGtp?.ProfileList?.length > 0 ? selectedGtp.ProfileList[0].PriceNodeId : ''}
      </Container>
      <AddObject isNew={false} isVisible={isModalVisible} onModalClose={() => setIsModalVisible(!isModalVisible)} />
    </div>
  )
}
